import PropTypes from 'prop-types'
import React, { Fragment, useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import MetaTags from 'react-meta-tags'
import { connect } from 'react-redux'
import { BreadcrumbsItem } from 'react-breadcrumbs-dynamic'
import { Elements, CardElement, useStripe, useElements } from '@stripe/react-stripe-js'
import { loadStripe } from '@stripe/stripe-js'

import { useToasts } from 'react-toast-notifications'
import { useHistory } from 'react-router'
import { useForm } from 'react-hook-form'
import Loader from 'react-loader-spinner'

import { CanadaStates } from '../../data/countryDropdown'
import { getDiscountPrice, getProductPrice } from '../../helpers/product'
import LayoutOne from '../../layouts/LayoutOne'
import Breadcrumb from '../../wrappers/breadcrumb/Breadcrumb'

import { addToCart, deleteAllFromCart } from '../../redux/actions/cartActions'
import { setLoader } from '../../redux/actions/loaderAction'

import stripeService from '../../services/stripe.service'
const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_PUBLISHABLE_KEY)

var paymentSecret
var customerStripeId
var emptyCartMethod

const CheckoutForm = ({ cartTotalPrice, cartItems, setLoader }) => {
  const stripe = useStripe()
  const elements = useElements()
  const { addToast } = useToasts()
  const history = useHistory()
  const [taxesObject, settaxesObject] = useState({})
  const [CountryDropdown, setCountryDropdown] = useState([])
  const [checkoutError, setCheckoutError] = useState()
  const [statusOfCard, setstatusOfCard] = useState()
  const [currency, setCurrency] = useState({
    currencySymbol: '$',
    currencyRate: 1
  })

  const [userError, setUserError] = useState('none')

  const {
    handleSubmit,
    register,
    errors,
    setValue,
    getValues,
    watch
  } = useForm()

  const one_US_to_CA_Dollar = 1.33
  useEffect(() => {
    // An effect function must not return anything besides a function
    async function fetchTaxesAndCountries () {
      const { taxes, error } = await stripeService.getCanadaTaxes()
      if (!error && taxes) {
        settaxesObject(taxes)
      }
      const {
        countries,
        countriesFetchErr
      } = await stripeService.getCountries()
      if (!countriesFetchErr && countries) {
        setCountryDropdown(countries)
      }
      setLoader(false)
    }
    fetchTaxesAndCountries()
  }, [setLoader, currency])
  let tax

  if (watch('country') === 'CA') {
    if (watch('region')) {
      if (taxesObject.hasOwnProperty(getValues('region'))) {
        tax = taxesObject[getValues('region')].tax
      }
    }
  } else {
    tax = 0
  }

  const handleCardDetailsChange = (ev) => {
    setstatusOfCard(ev.complete)
    ev.error ? setCheckoutError(ev.error.message) : setCheckoutError()
  }
  const renderOptions = (array) => {
    if (array.length) {
      return array.map((element, index) => {
        return (
          <option key={index} value={element.code}>
            {element.name}
          </option>
        )
      })
    }
  }

  const onSubmit = async (formInformation, e) => {
    let stripePaymentMethodDetails
    const {
      email,
      firstname,
      lastname,
      city,
      line1,
      country,
      region
      // postal_code,
    } = formInformation
    setLoader(true) // Intiating the loader
    setUserError('none')

    if (!statusOfCard) {
      addToast('Please enter the card details', {
        appearance: 'error',
        autoDismiss: true
      })
      setLoader(false) // Intiating the loader
      return
    }
    const selectedCurrency = getValues('currencyValue')
      ? getValues('currencyValue')
      : 'usd'

    if (!stripe || !elements) {
      // Stripe.js has not loaded yet. Make sure to disable
      // form submission until Stripe.js has loaded.
      return
    }

    // check if user by this email and username exists
    const isValidUser = await stripeService.checkValidUser({
      email: email
    })

    if (!isValidUser || !isValidUser.data || !isValidUser.data.success) {
      setLoader(false)
      // addToast("User Not Found", { appearance: "error", autoDismiss: true });
      setUserError('block')
      setValue('region', '')
      return
    }
    // generate payment intent
    const intent = await getPaymentIntent(selectedCurrency, tax, email)
    paymentSecret = intent
    if (!paymentSecret) return
    // Get a reference to a mounted CardElement. Elements knows how
    // to find your CardElement because there can only ever be one of
    // each type of element.
    const cardElement = elements.getElement(CardElement)
    let validatedPayment = true
    let stripeResult
    const stripeBillingDetails = {
      name: `${firstname} ${lastname}`,
      email: email,
      address: {
        city: city,
        line1: line1,
        country: country,
        state: region
        // postal_code: postal_code,
      }
    }

    try {
      const { error, paymentMethod } = await stripe.createPaymentMethod({
        type: 'card',
        card: cardElement,
        billing_details: stripeBillingDetails
      })
      if (!error && paymentMethod) {
        stripePaymentMethodDetails = paymentMethod
        stripeResult = await stripe.confirmCardPayment(paymentSecret, {
          payment_method: paymentMethod.id
        })
        if (stripeResult.error) throw stripeResult.error
      }
    } catch (paymentErr) {
      validatedPayment = false
      setLoader(false)
      return addToast('Error in Payment', {
        appearance: 'error',
        autoDismiss: true
      })
    } finally {
      if (validatedPayment) {
        // The payment succeeded!
        addToast('Payment Successfull', {
          appearance: 'success',
          autoDismiss: true
        })
        // call to update stuff here
        stripeService.updatePluginInfo({
          email: email,
          stripeResponse: stripeResult,
          stripeBillingDetails: stripeBillingDetails,
          tax,
          paymentMethod: stripePaymentMethodDetails,
          paymentIntent: stripeResult.paymentIntent.id,
          customerStripeId
        })

        emptyCartMethod(false)
        setLoader(false)
        return history.push({
          pathname: '/'
        })
      }
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)} className='row'>
      <div className='col-lg-7'>
        <div className='billing-info-wrap'>
          <h3>Billing Details</h3>
          <div className='row'>
            <div className='col-lg-6 col-md-6'>
              <div className='billing-info mb-20'>
                <label>
                  First Name <span className='required-data'>*</span>
                </label>
                <input
                  type='text'
                  name='firstname'
                  message='message for first name'
                  defaultValue=''
                  ref={register({
                    required: true
                  })}
                />
                {errors.firstname && (
                  <span className='required-data'>
                    Please provide first name
                  </span>
                )}
              </div>
            </div>
            <div className='col-lg-6 col-md-6'>
              <div className='billing-info mb-20'>
                <label>
                  Last Name <span className='required-data'>*</span>
                </label>
                <input
                  type='text'
                  name='lastname'
                  ref={register({
                    required: true
                  })}
                />
                {errors.lastname && (
                  <span className='required-data'>
                    Please provide last name
                  </span>
                )}
              </div>
            </div>
            <div className='col-lg-12'>
              <div className='billing-info mb-20'>
                <label>
                  Email <span className='required-data'>*</span>
                </label>
                <span style={{ fontSize: '10px' }}>
                  IMPORTANT:Please provide the same email address used in your
                  Keeper account
                </span>
                <input
                  type='email'
                  name='email'
                  ref={register({
                    required: 'Please provide email address',
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                      message: 'invalid email address'
                    }
                  })}
                />
                {errors.email && (
                  <span className='required-data'>{errors.email.message}</span>
                )}
                <span
                  className='required-data'
                  style={{
                    display: userError,
                    fontSize: '12px'
                  }}
                >
                  Keeper Account Not Found. Please provide the same email
                  address used in your Keeper account.
                </span>
              </div>
            </div>
            <div className='col-lg-12'>
              <div className='billing-info mb-20'>
                <label>Business name</label>
                <input type='text' name='username' ref={register} />
              </div>
            </div>
            <div className='col-lg-12'>
              <div className='billing-info mb-20'>
                <label>Street Address</label>
                <input
                  className='billing-address'
                  placeholder='House number and street name'
                  type='text'
                  name='line1'
                  ref={register}
                />
              </div>
            </div>
            <div className='col-lg-12'>
              <div className='billing-info mb-20'>
                <label>Town / City</label>
                <input type='text' name='city' ref={register} />
              </div>
            </div>
            <div className='col-lg-6 col-md-6'>
              <div className='billing-info mb-20'>
                <label>Country</label>

                <select
                  name='country'
                  ref={register({ required: true })}
                  onChange={(event) => {
                    // setCurrency({
                    //   ...currency,
                    //   currencyRate:
                    //     event.target.value === "CA" ? one_US_to_CA_Dollar : 1,
                    // });
                    // currencyValue
                    event.target.value === 'CA'
                      ? setValue('currencyValue', 'cad')
                      : setValue('currencyValue', 'us')
                  }}
                >
                  <option value=''>Select</option>
                  {renderOptions(CountryDropdown)}
                </select>

                {errors.country && (
                  <span className='required-data'>Please select country</span>
                )}
              </div>
            </div>

            <div className='col-lg-6 col-md-6'>
              <div className='billing-info mb-20'>
                <label>State / Province</label>
                {watch('country') === 'CA' ? (
                  <select name='region' ref={register({ required: true })}>
                    <option value=''>Select</option>
                    {renderOptions(CanadaStates)}
                  </select>
                ) : (
                  <input
                    type='text'
                    name='region'
                    ref={register({ required: true })}
                  />
                )}

                {watch('country') === 'CA' ? (
                  <>
                    {errors.region && (
                      <span className='required-data'>
                        Please select region
                      </span>
                    )}
                  </>
                ) : (
                  <>
                    {errors.region && (
                      <span className='required-data'>Please enter region</span>
                    )}
                  </>
                )}
              </div>
            </div>

            {/**
            <div className="col-lg-6 col-md-6">
              <div className="billing-info mb-20">
                <label>Postcode / ZIP</label>
                <input
                  type="text"
                  name="postal_code"
                  ref={register({ required: true })}
                />
                {errors.postal_code && (
                  <span className="required-data">
                    Please enter postal code.
                  </span>
                )}
              </div>
            </div>
 */}
            {watch('country') === 'CA' ? (
              <div className='col-lg-6 col-md-6'>
                <div className='billing-info mb-20'>
                  <label>Currency</label>
                  <select
                    name='currencyValue'
                    ref={register({
                      required: watch('country') === 'CA'
                    })}
                    onChange={(event) => {
                      setValue('currencyValue', event.target.value)
                      setCurrency({
                        ...currency,
                        currencyRate:
                          event.target.value === 'CA' ? one_US_to_CA_Dollar : 1
                      })
                    }}
                  >
                    <option value='cad'>CAD</option>
                    <option value='usd'>USD</option>
                  </select>

                  {errors.currencyValue && (
                    <span className='required-data'>
                      Please select currency
                    </span>
                  )}
                </div>
              </div>
            ) : (
              <></>
            )}
          </div>

          <h3>
            Card Details <span className='required-data'>*</span>
          </h3>
          <CardElement
            onChange={handleCardDetailsChange}
            options={{
              hidePostalCode: false,
              style: {
                base: {
                  fontWeight: 400,
                  fontStyle: 'normal',
                  lineHeight: '24px',
                  fontSize: '16px',
                  color: '#424770',
                  '::placeholder': {
                    color: '#aab7c4'
                  }
                },
                invalid: {
                  color: '#9e2146'
                }
              }
            }}
          />
          {/* {statusOfCard === undefined || !statusOfCard
            ? "display erro"
            : "norr"} */}
          {checkoutError && (
            <span className='required-data'>{checkoutError}</span>
          )}
        </div>
      </div>

      <div className='col-lg-5'>
        <div className='your-order-area'>
          <h3>Your order</h3>
          <div className='your-order-wrap gray-bg-4'>
            <div className='your-order-product-info'>
              <div className='your-order-top'>
                <ul>
                  <li>Product</li>
                  <li>Total</li>
                </ul>
              </div>
              <div className='your-order-middle'>
                <ul>
                  {cartItems.map((cartItem, key) => {
                    const currencySelected = watch('currencyValue')
                      ? watch('currencyValue')
                      : 'usd'
                    const itemPrice = getProductPrice(cartItem, currencySelected)
                    const discountedPrice = getDiscountPrice(
                      itemPrice,
                      cartItem.discount
                    )
                    const finalProductPrice = (
                      itemPrice * currency.currencyRate
                    ).toFixed(2)
                    const finalDiscountedPrice = (
                      discountedPrice * currency.currencyRate
                    ).toFixed(2)

                    discountedPrice != null
                      ? (cartTotalPrice +=
                        finalDiscountedPrice * cartItem.quantity)
                      : (cartTotalPrice +=
                        finalProductPrice * cartItem.quantity)
                    return (
                      <li key={key}>
                        <span className='order-middle-left'>
                          Wordpress Plugin X 1
                        </span>{' '}
                        <span className='order-price'>
                          {discountedPrice !== null
                            ? currency.currencySymbol +
                            (
                              finalDiscountedPrice * cartItem.quantity
                            ).toFixed(2)
                            : currency.currencySymbol +
                            (finalProductPrice * cartItem.quantity).toFixed(
                              2
                            )}
                        </span>
                      </li>
                    )
                  })}
                </ul>
                {/** GST and Tax Calculations */}
                {watch('country') === 'CA' && watch('region') ? (
                  <>
                    <ul>
                      <li>
                        <span className='order-middle-left'>GST ({tax}%)</span>
                        <span className='order-price'>
                          {(
                            tax *
                            0.01 *
                            cartTotalPrice.toFixed(2) *
                            currency.currencyRate
                          ).toFixed(2)}
                        </span>
                      </li>
                    </ul>
                  </>
                ) : (
                  <></>
                )}

                {/** GST and Tax Calculations -Ends */}
              </div>

              <div className='your-order-total'>
                <ul>
                  <li className='order-total'>
                    Total (
                    {watch('currencyValue') ? watch('currencyValue') : 'usd'})
                  </li>
                  {watch('region') ? (
                    <li>
                      {currency.currencySymbol +
                        (
                          tax * 0.01 * cartTotalPrice.toFixed(2) +
                          cartTotalPrice * currency.currencyRate
                        ).toFixed(2)}
                    </li>
                  ) : (
                    <li>
                      {currency.currencySymbol + cartTotalPrice.toFixed(2)}
                    </li>
                  )}
                </ul>
              </div>
            </div>
            <div className='payment-method' />
          </div>
          <div className='place-order mt-25'>
            <button className='btn-hover' type='submit' disabled={!stripe}>
              Pay
            </button>
          </div>
        </div>
      </div>
    </form>
  )
}

const ElementContainerCheckoutForm = ({
  cartTotalPrice,
  cartItems,
  // currency,
  setLoader
}) => {
  return (
    <Elements stripe={stripePromise}>
      <CheckoutForm
        cartTotalPrice={cartTotalPrice}
        cartItems={cartItems}
        // currency={currency}
        setLoader={setLoader}
      />
    </Elements>
  )
}

const NoItemsInCart = () => {
  return (
    <div className='row'>
      <div className='col-lg-12'>
        <div className='item-empty-area text-center'>
          <div className='item-empty-area__icon mb-30'>
            <i className='pe-7s-cash' />
          </div>
          <div className='item-empty-area__text'>
            No items found in cart to checkout <br />{' '}
            <Link to={process.env.PUBLIC_URL + '/shop-grid-standard'}>
              Shop Now
            </Link>
          </div>
        </div>
      </div>
    </div>
  )
}

const getPaymentIntent = async (currency, tax, email) => {
  paymentSecret = await stripeService.getPaymentSecret(currency, tax, email)

  if (paymentSecret && paymentSecret.data && paymentSecret.data.clientSecret) {
    console.log(paymentSecret.data)
    const receivedCustomerId = paymentSecret.data.customerId
    console.log('paymentSecret.data ', paymentSecret.data.clientSecret, receivedCustomerId)
    paymentSecret = paymentSecret.data.clientSecret
    customerStripeId = receivedCustomerId
  }
  return paymentSecret
}

const NewCheckout = ({
  location,
  cartItems,
  deleteAllFromCart,
  loaderActive,
  setLoader
}) => {
  emptyCartMethod = deleteAllFromCart
  const { pathname } = location
  const cartTotalPrice = 0
  // getPaymentIntent();
  // setLoader(false);
  return (
    <>
      <MetaTags>
        <title>Keeper Memorials - Online Store | Checkout</title>
        <meta
          name='description'
          content='Checkout page of Keeper Memorials - Online Store'
        />
      </MetaTags>
      <BreadcrumbsItem to={process.env.PUBLIC_URL + '/'}>
        Product Page
      </BreadcrumbsItem>
      <BreadcrumbsItem to={process.env.PUBLIC_URL + pathname}>
        Checkout
      </BreadcrumbsItem>
      {/* <button onClick={() => setLoader(false)}>setLoader</button> */}
      <Loader
        type='ThreeDots'
        color='#00BFFF'
        secondaryColor='Grey'
        height={300}
        width={300}
        visible={loaderActive}
        style={{
          position: 'fixed',
          width: '100%',
          height: '100%',
          opacity: '0.5',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          backgroundColor: 'grey',
          zIndex: '1'
        }}
      />
      <LayoutOne headerTop='visible'>
        {/* breadcrumb */}
        <Breadcrumb />
        <div className='checkout-area pt-95 pb-100'>
          <div className='container'>
            {cartItems && cartItems.length >= 1 ? (
              <ElementContainerCheckoutForm
                cartTotalPrice={cartTotalPrice}
                cartItems={cartItems}
                setLoader={setLoader}
              />
            ) : (
              <NoItemsInCart />
            )}
          </div>
        </div>
      </LayoutOne>
    </>
  )
}

NewCheckout.propTypes = {
  cartItems: PropTypes.array,
  currency: PropTypes.object,
  location: PropTypes.object,
  loaderActive: PropTypes.bool,
  setLoader: PropTypes.func
}

const mapStateToProps = (state) => {
  return {
    cartItems: state.cartData,
    currency: state.currencyData,
    loaderActive: state.loader.active
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    deleteAllFromCart: (addToast) => {
      dispatch(deleteAllFromCart(addToast))
    },
    setLoader: (value) => {
      dispatch(setLoader(value))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(NewCheckout)
